* {
  box-sizing: border-box;
  padding: 0;
}

.container {
  &-table {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 300px;
    padding: 5px;
  }

  &-switcher {
    display: flex;
    height: 30px;
    padding: 4px;
    border: 1px solid #000;
    justify-content: space-between;
    align-items: center;

    &__left,
    &__right {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 24px;
      cursor: pointer;
    }

    &__left:hover,
    &__right:hover {
      background-color: #ccc;
    }
  }
}

.options {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  box-shadow: 0px -5px 5px -5px rgba(34, 60, 80, 0.6) inset;

  &__item {
    padding: 0 5px 5px 5px;
    display: flex;
  }

  &__item:hover {
    background-color: #ccc;
    cursor: pointer;
  }

  &__item:active {
    background-color: #ccc;
  }
}

.nav {
  display: flex;
  flex-direction: column;
}

.menu {
  margin: 0;
  overflow-y: scroll;
  height: 370px;
  display: flex;
  flex-direction: column;

  &-item {
    padding: 5px 5px 0 5px;
    box-shadow: 0px -5px 5px -5px rgba(34, 60, 80, 0.6) inset;
    cursor: pointer;
  }
}

.visibility {
  display: none;
}

.active {
  background-color: #ccc;
}
